@font-face {
  font-family: 'Helvetica Neue';
  src: url('../assets/fonts/HelveticaNeueBoldCondensed.woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Helvetica Neue';
  src: url('../assets/fonts/HelveticaNeueBlackCondensed.woff');
  font-weight: 900;
  font-style: normal;
}

*,
*::before,
*::after{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  background-color: #EBECF0;
}

.App{
  margin-top: 50px;
  margin-bottom: 50px;
  margin-left: 100px;

  display: flex;
  justify-content: flex-start;

}

#overlay {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:#000;
  opacity: 0.2;
  filter: alpha(opacity=10);

  transition: all ease-in-out 0.1s;
}

.app__switcher-container{
  width: 800px;

  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}